  line-height:1.8;
}

nav.show , nav.hide{
  transition: all 0.8s ease-in-out;
}
nav.show ul , nav.hide ul{
  width:100%;
}
nav.show ul li, nav.hide ul li {
  text-align: center;
}
nav.show {
  opacity:0.9; 
  max-height: 25rem;
  -webkit-transform: scaleY(1);
          transform: scaleY(1);
}

nav.hide{
  max-height: 0;
  opacity:0;
  -webkit-transform: scaleY(0);
          transform: scaleY(0);
  padding-top :0;
  padding-bottom:0 ;
}
header nav div{
  display:flex;
}
header nav div ul{
  display:flex;
}
ul li{
  padding-left:1rem;
}

