.navbar-toggler {
    display: none;
    border: none;
    background: transparent !important;
  }
  .navbar-toggler:focus {
    outline: none;
    background: transparent !important;
  }
.navbar-toggler .icon-bar {
    background-color: #fff;
    -webkit-transform: rotate(0deg) translate(0px, 0px);
            transform: rotate(0deg) translate(0px, 0px);
    transition: ease all .2s;
  }
.navbar-toggler .icon-bar {
    display: block;
    width: 22px;
    height: 2px;
    border-radius: 1px;
  }
.navbar-toggler .icon-bar+.icon-bar {
    margin-top: 4px;
  }
  
  .navbar-toggler:active>.icon-bar:nth-child(2) {
    width: 22px;
    transition: ease all .2s;
  }
  
  .navbar-toggler:not(.collapsed) .icon-bar:nth-child(1) {
    -webkit-transform: rotate(270deg) translate(-8px, 4px);
            transform: rotate(270deg) translate(-8px, 4px);
    transition: ease all .2s;
  }
  
  .navbar-toggler:not(.collapsed) .icon-bar:nth-child(2) {
    opacity: 0;
    transition: ease all .2s;
  }
  
  .navbar-toggler:not(.collapsed) .icon-bar:nth-child(3) {
    -webkit-transform: rotate(360deg) translate(4px, -4px);
            transform: rotate(360deg) translate(4px, -4px);
    transition: ease all .2s;
  }

  .navbar-toggler.collapsed {
    border:none;
    -webkit-animation:  toggler-spin infinite 15s linear;
            animation:  toggler-spin infinite 15s linear;
  }

  @-webkit-keyframes toggler-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }

  @keyframes toggler-spin {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @media only screen and (min-width:1024px){
    nav.show {
      display: none;
    }
  }
  @media only screen and (max-width:1024px){
    .navbar-toggler{
      display: inline-block;
    }
    
    ul.nav-collapsed{
      display: none;
    }
  }
  
