.modal {
    position: fixed;
    top: 0;
    left: 0;
    width:100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.6);
  }
  
  .modal-main {
    position:fixed;
    background: white;
    width: 50vw;
    height: 80vh;
    top:50%;
    left:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    padding: 3rem;
    overflow-y: scroll;
  }
  
  .display-block {
    display: block;
  }
  
  .display-none {
    display: none;
  }

  .modal-close{
      position: absolute;
      right:1rem;
      top:1rem;
      background-color: transparent;
      border : none;
  }

  @media only screen and (max-width:1024px){
    
    .modal-main{
        width:80vw;
    }
}
